export default class Utils {

    setMap(data, rows, map) {
        var pagina = 0;
        var cantXPage = rows;
        var citases = [];
        var cant = cantXPage;
        data.forEach((tT_CitasTelPre) => {
            if (cant == 0) {
                cant = cantXPage;
                citases = [];
                pagina++;
            }
            citases.push(tT_CitasTelPre);
            map.set(pagina, citases);
            cant--;
        });
        return map;
    }

    findByValue(data, value) {
        console.log( data)
        if (value == null || data == undefined || data.length == 0) {
             return data;
        }
      
        var result = data.filter(x=>
            x.DOCUMENTO != undefined ? x.DOCUMENTO.toString().match(value) : "" ||
            x.TM15NDEUOPE != undefined ? x.TM15NDEUOPE.toString().match(value) : "" ||
            x.TM07SCOSDES != undefined ? x.TM07SCOSDES.match(value) : "" ||
            x.MONEDADEUDA != undefined ?  x.MONEDADEUDA.match(value) : "" ||
            x.VSUPERVISOR != undefined ? x.VSUPERVISOR.match(value) : ""||
            x.VGESTOR != undefined ? x.VGESTOR.match(value) : "" ||
            x.GESTORGESTION != undefined ?  x.GESTORGESTION.match(value)  : ""
        );
        console.log(result)
        return result;
    }
}